import { useEffect } from "react";
import { useHistory } from "react-router-dom";
import { scrollTo } from "../utils";

/**
 * @see https://stackoverflow.com/a/54343182/5506547
 */
const RouterScrollToTop = () => {
  const {
    listen
  } = useHistory();
  useEffect(() => {
    const destroy = listen(() => {
      scrollTo(0);
    });
    return () => {
      destroy();
    };
  }, [listen]);
  return null;
};
export { RouterScrollToTop };